.wrapper {
  display: flex;
  flex-direction: colum;
  justify-content: center;
}

.button {
  outline: none;
  border: none;
  padding: 14px 24px;
  color: #ffffff;
  background: #007cc2;
  border-radius: 96px;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  font-family: 'Satoshi';
  font-style: normal;
  cursor: pointer;
  transform: scale(1);
  transition: all 0.05s ease-in-out;
}

.button:hover {
  transform: scale(1.03);
}

.button:active {
  transform: scale(1);
}

.input {
  outline: none;
  background: #ffffff;
  border: 1px solid #e6e6e6;
  border-radius: 12px;
  padding: 10px 20px;
  font-family: 'Satoshi';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  opacity: 0.33;
}

.verificationWrapper {
  display: block;
}

.singleCharacter {
  max-width: 36px;
  height: 48px;
  font-family: 'Satoshi';
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  color: #555555;
  border: 1px solid #cccccc;
  background: transparent;
  border-radius: 8px;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.singleCharacterSelected {
  border: 2px solid #cccccc;
}

.verificationContainer {
  max-width: 280px;
  gap: 0.6rem;
  justify-content: space-between;
}
